import { Component, OnInit} from '@angular/core';
import { PrepService } from '../mock.service';
import { Router, ActivatedRoute } from '@angular/router';
import { FormsModule }   from '@angular/forms';
import { Headers, Http } from '@angular/http';
import 'rxjs/add/operator/toPromise';
import { CookieUtil } from '../cookieUtil';


declare var $:any;


@Component({
  selector: 'app-regist',
  templateUrl: './regist.component.html',
  styleUrls: ['./regist.component.css']
})
export class RegistComponent implements OnInit {

  user={name:"",nickname:"",mail:"",phone:"",password:"",}
  name:string
  password:string
  confirm:string

  jsonData:string
  resData={code:"",nickname:""}
  private headers = new Headers({'Content-Type': 'application/x-www-form-urlencoded'});

  fade(): void{
		this.ps.isNext=0;
	    $("#registFont1").slideDown(500,()=>{

        	$("#registFont2").slideDown(500,()=>{

        		$("#submit1").slideDown(500,()=>{

        				this.inputFade();
        			})
        	});
    });
	      	
   }
  inputFade():void{

  	$("#input1").animate({left: "0px"},200,()=>{
  			$("#input2").animate({left: "0px"},200,()=>{
  					$("#input3").animate({left: "0px"},200)
  				});
  		});
  	
  }

  nextFade():void{
  	$("#submit2").slideDown(500,()=>{
  		$("#input4").animate({left: "0px"},200,()=>{
  			$("#input5").animate({left: "0px"},200,()=>{
  				$("#input6").animate({left: "0px"},200);
  			});
  		});
    });
  }

  next():void{

  	this.user.name = this.name
  	// console.log(this.name+this.password+this.confirm);
  	
  	if(this.password==this.confirm) {
  		this.user.password=this.password
  		this.ps.isNext=1;
  		this.nextFade()
  	}else{

  		alert("两次密码输入不相同")
  	}

  }

  submit():void{

	this.jsonData="username="+this.user.name+"&password="+this.user.password
				 +"&mail="+this.user.mail+"&phone="+this.user.phone
				 +"&nickname="+this.user.nickname
    
	console.log(this.jsonData);
    this.http.post("http://www.datamath.cn/Home/Index/registAction",this.jsonData
                    ,{headers:this.headers})
    .toPromise()
    .then(response=>{
          this.resData=response.json()
          console.log(this.resData);
          if(this.resData.code=="failed") {
             alert("用户已经存在或不能为空")
             this.ps.isNext=0;
             this.router.navigate(['/login'], { relativeTo: this.route })
          }else{

            CookieUtil.setCookie("nickname",this.resData.nickname)
            this.router.navigate(['/index'], { relativeTo: this.route })

          }
      })

  }

  constructor(private http: Http,private ps:PrepService,private router: Router,private route: ActivatedRoute) { }

  ngOnInit() {
  	 this.fade();
  }

}



