export class CookieUtil {
  //设置cookie
  static setCookie(name, value){
    //opts maxAge, path, domain, secure
    if(name && value){
      var cookie = encodeURIComponent(name) + '=' + encodeURIComponent(value)+";path=/;";
      document.cookie = cookie;
      return cookie;
    }else{
      return '';
    }
  }
  //获取cookie
  static getCookie(name){
    return decodeURIComponent(this.getCookiesObj()[name]) || "";
  }
  static getCookiesObj(){
    var cookies = {};
    if(document.cookie){
      var objs = document.cookie.split('; ');
      for(var i in objs){
        var index = objs[i].indexOf('='),
          name = objs[i].substr(0, index),
          value = objs[i].substr(index + 1, objs[i].length);
        cookies[name] = value;
      }
    }
    return cookies;
  }
  //清除某个cookie
  static remove(name){
  if(this.getCookiesObj()[name]){
    document.cookie = name + '=; max-age=0;path=/;';
  }
  };

  //删除cookies
  static deleteCookie(name:any){
    var date=new Date();
    date.setTime(date.getTime()-1000000);
    document.cookie=name+"=v;expires="+date.toUTCString()+";path=/;";
  }


  //清除所有cookie
  static clear(){
  var cookies = this.getCookiesObj();
  for(var key in cookies){
    document.cookie = key + '=; max-age=0;path=/;';
  };
  };
}



