import { Component, OnInit,OnChanges, Input, trigger, state, style, animate, transition } from '@angular/core';

import { CookieUtil } from '../cookieUtil';
import { Router, ActivatedRoute } from '@angular/router';

declare var $:any;


@Component({
  selector: 'app-matrix',
  templateUrl: './matrix.component.html',
  styleUrls: ['./matrix.component.css']
})
export class MatrixComponent implements OnInit {
  cnt: number;
  a: number;
  time1: number;
  time2: number;

  map=["人工智能","算法世界","物联网","数据分析","高等数学","API测试","coming","财数谷","讨论区","全球定位"]

  ran(a): void{

    CookieUtil.setCookie('areaNo',this.map[a-1]);
    this.router.navigate(['/datamath'], { relativeTo: this.route })

  }
 
  rollback(a): void{
    $("#m"+a).find("#back").fadeIn(500)

  }

  turnback(a): void{
    $("#m"+a).find("#back").fadeOut(500)

  }

  randomStart(): void{

	    $("#m_search").find("input").val("Running!");
	    $("#m_search").find("input").attr("disabled", true);
	    this.cnt = Math.round(Math.random()*100+30)
	    this.a = Math.round(Math.random()*9+1)
	    this.time1 = setInterval(()=>{

	    	
	        if(this.a==10){
	            $("#m"+this.a).find("#back").fadeIn(500)
	            $("#m"+this.a).find("#back").fadeOut(500)
	            this.a=1
	            this.cnt--;

	        }else {
	            $("#m"+this.a).find("#back").fadeIn(500)
	            $("#m"+this.a).find("#back").fadeOut(500)
	            this.cnt--;
	            this.a++;
	        }
	        if(this.cnt==5){
	            clearInterval(this.time1)
	            this.time2 = setInterval(()=>{
	                if(this.a==10){
	                    $("#m"+this.a).find("#back").fadeIn(500)
	                    $("#m"+this.a).find("#back").fadeOut(500)
	                    this.a=1
	                    this.cnt--;

	                }else {
	                    $("#m"+this.a).find("#back").fadeIn(500)
	                    $("#m"+this.a).find("#back").fadeOut(500)
	                    this.cnt--;
	                    this.a++;
	                }
	                if(this.cnt==0){
	                    clearInterval(this.time2)
	                    $("#m_search").find("input").removeAttr("disabled");
	                    $("#m_search").find("input").val("感受下随机的乐趣");
	                    this.ran(this.a-2)
	                }
	            },1000)
	        }
	    },100)
  }
  constructor(private router: Router,private route: ActivatedRoute) { 

  }

  ngOnInit() {


  }

}



