import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { InMemoryWebApiModule } from 'angular-in-memory-web-api';
import { InMemoryDataService}  from './mock.service';
import { HttpModule }    from '@angular/http';
import { FormsModule }   from '@angular/forms';
import {PathLocationStrategy,LocationStrategy,HashLocationStrategy} from '@angular/common'
import { CommonModule }     from '@angular/common';
import { FileUploadModule } from 'ng2-file-upload';


import { AppComponent } from './app.component';
import { TitleComponent } from './title/title.component';
import { LoginComponent } from './login/login.component';
import { HomeComponent } from './home/home.component';
import { Page404Component } from './page404/page404.component';
import { BannerComponent } from './banner/banner.component';
import { LoginPanelComponent } from './login-panel/login-panel.component';
import { RegistPanelComponent } from './regist-panel/regist-panel.component';
import { MatrixComponent } from './matrix/matrix.component';
import { FootComponent } from './foot/foot.component';
import { RegistComponent } from './regist/regist.component';
import { DatamathComponent } from './datamath/datamath.component';
import { DatalistComponent } from './datalist/datalist.component';
import { DatafootComponent } from './datafoot/datafoot.component';
import { CosineDistanceComponent } from './detial/cosine-distance/cosine-distance.component';
import { Stock30daysComponent } from './detial/stock30days/stock30days.component';
import { Algorithm1Component } from './detial/algorithm1/algorithm1.component';
import { UploadComponent } from './upload/upload.component';
import { DecisionComponent } from './detial/decision/decision.component';

const childRoutes: Routes = [

  {path: 'api1', component: CosineDistanceComponent},
  {path: 'api2', component: Stock30daysComponent},
  {path: 'api3', component: DecisionComponent},
  {path: 'algorithm1', component: Algorithm1Component},
  {path:  '**', component: Page404Component }


];

const routes: Routes = [
  { path: '', redirectTo: 'index', pathMatch: 'full' },
  { path: 'index',  component: HomeComponent },
  { path: 'login', component: LoginComponent },
  { path: 'regist', component: RegistComponent },
  { path: 'upload', component: UploadComponent },  
  { path: 'datamath', 
    component: DatamathComponent,
    children: childRoutes},  
  { path:  '**', component: Page404Component }
];



@NgModule({
  declarations: [
    AppComponent,
    TitleComponent,
    LoginComponent,
    HomeComponent,
    Page404Component,
    BannerComponent,
    LoginPanelComponent,
    RegistPanelComponent,
    MatrixComponent,
    FootComponent,
    RegistComponent,
    DatamathComponent,
    DatalistComponent,
    DatafootComponent,
    CosineDistanceComponent,
    Stock30daysComponent,
    Algorithm1Component,
    UploadComponent,
    DecisionComponent
  ],
  imports: [
    BrowserModule,
    RouterModule.forRoot(routes),
    HttpModule,
    InMemoryWebApiModule.forRoot(InMemoryDataService,{passThruUnknownUrl:true}),
    FormsModule,
    FileUploadModule,
    CommonModule
  ],
   providers: [
    {provide:
     LocationStrategy,useClass:
     HashLocationStrategy}
  ],
  bootstrap: [AppComponent]
})
export class AppModule { }



