import { Component, OnInit,Injectable,Input,Output,EventEmitter } from '@angular/core';
import { FormsModule }   from '@angular/forms';
import { Headers, Http } from '@angular/http';
import 'rxjs/add/operator/toPromise';
import { UserInfo } from '../app.class';
import { Router, ActivatedRoute } from '@angular/router';


import { PrepService } from '../mock.service';
import { CookieUtil } from '../cookieUtil';



declare var $:any;

@Component({
  selector: 'app-login-panel',
  templateUrl: './login-panel.component.html',
  styleUrls: ['./login-panel.component.css']
})


export class LoginPanelComponent implements OnInit {

  user:UserInfo;
  private heroesUrl = 'api/userinfo';
  userinfo:UserInfo[]
  islogin:number
  private headers = new Headers({'Content-Type': 'application/x-www-form-urlencoded'});
  jsonData:string
  resData={code:"",nickname:""}
  //private cookie:CookieUtil;

  nickname:string

  constructor(private http: Http,private router: Router,private route: ActivatedRoute,private ps:PrepService) { 

    this.user={name:"",password:""};

  }

  postUserInfo():void{
   



    this.jsonData="username="+this.user.name+"&password="+this.user.password
    

    this.http.post("http://www.datamath.cn/Home/Index/loginAction",this.jsonData
                    ,{headers:this.headers})
    .toPromise()
    .then(response=>{
          this.resData=response.json()
          console.log(this.resData);
          
          if(this.resData.code=="failed") {
             alert("用户名密码错误")
          }else{

            CookieUtil.setCookie("nickname",this.resData.nickname)

            this.router.navigate(['/index'], { relativeTo: this.route })

          }
          
      })

  }


  private handleError(error: any): Promise<any> {
    console.error('An error occurred', error); 
    return Promise.reject(error.message || error);
  }



  ngOnInit() {

      this.islogin=0;

  }

}



