import { Component, OnInit,Input } from '@angular/core';
import { PrepService } from '../mock.service';
import { CookieUtil } from '../cookieUtil';

declare var $:any;

@Component({
  selector: 'app-title',
  templateUrl: './title.component.html',
  styleUrls: ['./title.component.css']
})
export class TitleComponent implements OnInit {

  nickname:string

	navListover(){

    	$("#navList").css("display","block");
  	 	// $("title_panel").css("background-color",": 2c2c2c");
	}

	navListleave(){

    	setTimeout(()=> {
        	$("#navList").css("display","none");
    	},2000);

	}
	chkIn(){

    	$("#navList").attr("id","navListBlock");
    	$("#title_panel").attr("id","titlePanel");

	}

	chkOut() {

    	$("#navListBlock").attr("id","navList");
    	$("#titlePanel").attr("id","title_panel");
    	$("#navList").css("display","none");
	}

  quit(){

    CookieUtil.remove('nickname')
    
  }

  constructor(private ps:PrepService) { 
    
    
  }

  ngOnInit() {

     this.nickname=CookieUtil.getCookie('nickname');
     if(this.nickname=="undefined") {
       this.nickname=""
     }
      
  }

}



