import { Component, OnInit } from '@angular/core';

import { CookieUtil } from '../cookieUtil';

@Component({
  selector: 'app-datamath',
  templateUrl: './datamath.component.html',
  styleUrls: ['./datamath.component.css']
})
export class DatamathComponent implements OnInit {

  areaNo:string;

  constructor() { }

  ngOnInit() {

  	this.areaNo = CookieUtil.getCookie('areaNo');

  }

}



