import { Component, OnInit } from '@angular/core';
import { Headers, Http } from '@angular/http';

@Component({
  selector: 'app-cosine-distance',
  templateUrl: './cosine-distance.component.html',
  styleUrls: ['./cosine-distance.component.css']
})
export class CosineDistanceComponent implements OnInit {

  lng1:string
  lat1:string
  lng2:string
  lat2:string

  jsonData:any
  private headers = new Headers({'Content-Type': 'application/json'});
  resData={distance:""}
  distance:string

  postUserInfo():void{
     


      
      this.jsonData={"lng1":parseFloat(this.lng1),"lat1": parseFloat(this.lat1),"lng2":parseFloat(this.lng2),"lat2": parseFloat(this.lat2)}
      // this.jsonData={"lng1":121.622650,"lat1": 31.204218,"lng2":121.625428,"lat2": 31.204218}
      // console.log(this.jsonData);
      this.http.post("http://www.datamath.cn:8088/earthDistance",this.jsonData
                      ,{headers:this.headers})
      .toPromise()
      .then(response=>{
         
            this.distance=response.json().distance+"米"
            console.log(this.distance);

        })

    }

  constructor(private http: Http) { }

  ngOnInit() {

  }

}



