import { Component, OnInit } from '@angular/core';
import { Headers, Http } from '@angular/http';

@Component({
  selector: 'app-decision',
  templateUrl: './decision.component.html',
  styleUrls: ['./decision.component.css']
})
export class DecisionComponent implements OnInit {

  stockName:string
  jsonData:any
  private headers = new Headers({'Content-Type': 'application/json'});
  result:string
  midsmallcounts:string
  midmediumcounts:string
  smallcounts:string
  mediumcounts:string

  getStockInfo():void{
     


      
	  this.jsonData={"name":this.stockName}

      // console.log(this.jsonData);
      this.http.post("http://www.datamath.cn:5000/selected",this.jsonData,{headers:this.headers})
      .toPromise()
      .then(response=>{
         
            console.log(response.json()['dicision']);
            this.result = "如果前一日的小单净额和中单净额满足:"+response.json()['result']+","+"那么投资策略为:"+response.json()['dicision']
            this.midsmallcounts = "30日内小单净额中位数:"+response.json()['midsmallcounts']
            this.midmediumcounts = "30日内中单净额中位数:"+response.json()['midmediumcounts']
            this.smallcounts = "前一日小单净额:"+response.json()['smallcounts']
            this.mediumcounts = "前一日中单净额:"+response.json()['mediumcounts']

        })

    }

  constructor(private http: Http) { }

  ngOnInit() {
  }

}



